
import { Course } from '@/types'
import { defineComponent } from 'vue'
import Week from './components/Week/Week.vue'

export default defineComponent({
  name: 'CourseOverview',
  components: { Week },
  props: {
    courseId: {
      type: String,
      required: true
    }
  },
  computed: {
    course (): Course | undefined {
      return this.$store.getters.courseByID(this.courseId)
    }
  }
})
