
import { defineComponent } from 'vue'

export default defineComponent({
  name: 'SVGCheckbox',
  props: {
    staticCheck: {
      type: Boolean,
      default: false
    },

    modelValue: {
      type: Boolean,
      default: false
    }
  },
  emits: ['update:modelValue'],
  methods: {
    handleToggle (): void {
      if (!this.staticCheck) {
        this.$emit('update:modelValue', !this.modelValue)
      }
    }
  }
})
